import { Component, Prop, Event, EventEmitter, h, Host } from '@stencil/core';

export type ModalWidth = 'sm' | 'md' | 'lg' | 'xl' | 'xxl';

const MODAL_WIDTHS: Record<ModalWidth, string> = {
  sm: '400px',
  md: '480px',
  lg: '520px',
  xl: '640px',
  xxl: '688px',
};

@Component({
  tag: 'bh-modal',
  styleUrl: 'bh-modal.css',
  shadow: true,
})
export class BhModal {
  /**
   * Whether the modal is visible
   */
  @Prop() visible: boolean = false;

  /**
   * Whether to show the backdrop overlay
   */
  @Prop() overlay: boolean = true;

  /**
   * Preset width of the modal: sm (400px), md (480px), lg (520px), xl (640px), xxl (688px)
   */
  @Prop() width: ModalWidth = 'sm';

  /**
   * Emitted when the modal is closed (overlay click or close button)
   */
  @Event() bhClose!: EventEmitter<void>;

  private handleClose = (): void => {
    this.bhClose.emit();
  };

  private handleOverlayClick = (event: MouseEvent): void => {
    if (event.target === event.currentTarget) {
      this.handleClose();
    }
  };

  render() {
    if (!this.visible) return null;

    const modalClasses = {
      'modal-wrapper': true,
      'modal-with-overlay': this.overlay,
    };

    return (
      <Host>
        <div class={modalClasses} onClick={this.handleOverlayClick}>
          {this.overlay && <div class="modal-overlay" />}

          <div class="modal" style={{ maxWidth: MODAL_WIDTHS[this.width] }}>
            <slot name="header" />
            <slot />
            <slot name="actions" />
          </div>
        </div>
      </Host>
    );
  }
}
